.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
table {
  border-spacing: 0;
  width: 100%;
  border: 1px solid #ddd;
}

th, td {
  text-align: left;
  padding: 16px;
}

tr:nth-child(even) {
  background-color: #f2f2f2
}
.navigation {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0.5rem 0rem;
  background-color: #fff;
  color: black;
  box-shadow: 0 2px 2px 2px rgba(9, 9, 9, 0.23);
}
.navigation ul {
  float: right;
  margin-right: 20px;
}

.navigation ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
}

.navigation ul li a {
  color: black;
  font-size: 30px;
  padding: 7px 13px;
  border-radius: 7px;
  text-decoration: none;
}

a.active,
a:hover {
  background: turquoise;
  transition: .5s;
}

.logo {
  width: 200px;
  height: 200px;
  position: absolute;
  top: -10px;
  left: 5px;
}